header.header {
    background-color: #333;
    color: white;
    padding: 1rem;
    margin: 0;
}

.brand {
    text-transform: uppercase;
    text-shadow: 2px 2px 2px #333;
    line-height: 2em;
    font-weight: bold;
}

.brand:first-letter {
    color: red;
    font-size: 2rem;
}

.content {
    margin: 2rem 0;
}

textarea#add-text,
button#add-text-btn {
    margin: 1rem 0;
    padding: 1rem;
    width: 100%;
    border: none;
    border-bottom: hotpink solid thick;
}

button#add-text-btn {
    cursor: pointer;
}

.stamps .ts {
    border-bottom: deeppink solid thin;
}
.stamps .note {
    white-space: pre-wrap;
    margin: 1rem 0;
}